.tab-item {
  position: relative;
  color: #333;
}
.list-item {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 55px;
  padding: 0 10px;
  border-bottom: 1px solid #eee;
}
.no-result {
  position: absolute;
  top: 40%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
  color: #666;
}
.no-result p {
  transform: translateX(5px);
}
.songs li {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 55px;
  padding: 0 10px;
  border-bottom: 1px solid #eee;
}
.songs li .left {
  width: 80%;
}
.songs li .left .title {
  font-size: 16px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  line-height: 1.5;
}
.songs li .left .info {
  font-size: 12px;
  color: #666;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.albums li,
.artists li,
.playlists li {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 55px;
  padding: 0 10px;
  border-bottom: 1px solid #eee;
}
.albums li img,
.artists li img,
.playlists li img {
  width: 45px;
  height: 45px;
  margin-right: 10px;
}
.albums li .info,
.artists li .info,
.playlists li .info {
  flex-grow: 1;
}
.albums li .info > div,
.artists li .info > div,
.playlists li .info > div {
  width: 85%;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.albums li .info .sub-info,
.artists li .info .sub-info,
.playlists li .info .sub-info {
  font-size: 12px;
  margin-top: 5px;
  color: #666;
}
