.container {
  min-height: 100%;
  background-color: #fff;
  color: #333;
}
.container .hot-list-box {
  padding: 15px 10px 0;
}
.container .hot-list-box ul {
  margin: 10px 0;
}
.container .hot-list-box ul li {
  display: inline-block;
  height: 32px;
  margin-right: 8px;
  margin-bottom: 8px;
  padding: 0 14px;
  font-size: 14px;
  line-height: 32px;
  border: 1px solid #d3d4da;
  border-radius: 32px;
  background: #fbfcfd;
}
.container .hot-list-box ol li {
  display: flex;
  align-items: center;
  height: 45px;
}
.container .hot-list-box ol li div:first-child {
  margin-right: 10px;
  font-size: 15px;
  color: #666;
}
.container .hot-list-box ol li div:nth-child(2) {
  flex-grow: 1;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.container .hot-list-box ol li div:last-child {
  margin: 0 10px;
  font-size: 15px;
  color: #666;
}
.container .suggest-box li {
  display: flex;
  align-items: center;
  height: 45px;
}
.container .suggest-box li div:first-child {
  margin: 0 10px;
  color: #666;
}
.container .suggest-box li div:last-child {
  flex-grow: 1;
  padding-right: 10px;
  height: 45px;
  line-height: 45px;
  border-bottom: 1px solid #d3d4da;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
